<template>
	<div class="modal fade" id="careerModal" tabindex="-1" role="dialog" aria-labelledby="careerModalTitle" aria-hidden="true">
		<div class="modal-dialog" role="document">
			<div class="modal-content">
				<div class="modal-header modal__header">
					<h3 class="modal-title" id="careerModalTitle">Add Career</h3>
					<button type="button" class="close" data-dismiss="modal" aria-label="close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body modal__body">
					<form 
						@submit.prevent="createCareer"
						class="modal__form" 
						method="post">
						<div class="form-group">
							<label class="modal__label" for="career">Title</label>
							<input 
								type="text" 
								:class="[{'is-invalid': errors.name}]"
								class="form-control" 
								id="career" 
								placeholder="Career Title"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.name">
							<div class="invalid-feedback">
								{{ errors.name }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="career">Career Segment</label>
							<select 
								class="custom-select form-control"  
								id="category"
								:class="[{'is-invalid': errors.segment}]"
								v-model="itemData.career_segment_id">
								<option selected disabled>Select a career segment for this career</option>
								<option 
									v-for="(segment, i) in careerSegments"
									:key="i"
									:value="segment.id">{{segment.name}}</option>
							</select>
							<div class="invalid-feedback">
								{{ errors.career_segment_id }}
							</div>
						</div>
					</form>
				</div>
				<div class="modal-footer modal__footer">
					<Loader v-if="loading"/>
					<button 
						@click="clearErrors()"
						class="btn btn--md" 
						type="button"
						data-dismiss="modal"
						aria-label="close"
						name="button">Cancel</button>
					<button 
						@click.prevent="createCareer"
						class="btn btn--md btn__primary" 
						type="submit" 
						name="button">
						Save Career</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import form from '../../mixins/form';
import Loader from '../../components/Loader';
import { createCareer, getAllCareerSegment } from '../../utils/api/career';
import classes from '../../utils/validations/classes';
import { success, fail } from '../../utils/toast';
import eventHub from '../../utils/eventhub';

export default {
	name: 'AddCareer',
	mixins: [form],
	data() {
		return {
			itemData: {},
			errors: {},
			loading: false,
			careerSegments: [],
			loadingCareerSegments: false,
			careerSegmentsLoaded: false
		};
	},
	components: {
		Loader
	},
	watch: {
		issues(val) {
			this.errors = { ...val };
		}
	},
	methods: {
		createCareer() {
			this.validate(this.itemData, classes.career);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			createCareer(this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.itemData = {};
						this.loading = false;
						eventHub.$emit('career-created', response.data);
						success({
							title: 'Success! A new career has been created'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Oops! Something went wrong'
					});
					return error;
				});
			return true;
		},
		getAllCareerSegment() {
			this.loadingCareerSegments = true;
			getAllCareerSegment()
				.then((response) => {
					if (response.status === 200) {
						this.careerSegments = response.data.data;
						this.careerSegmentsLoaded = true;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.loadingCareerSegments = false;
					return error;
				});
			return true;
		}
	},
	created() {
		this.getAllCareerSegment();
	}
};
</script>
